

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
  background: rgb(149,149,149); /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(149,149,149,1) 0%,rgba(13,13,13,1) 34%,rgba(1,1,1,1) 50%,rgba(10,10,10,1) 53%,rgba(78,78,78,1) 76%,rgba(56,56,56,1) 87%,rgba(27,27,27,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, rgba(149,149,149,1) 0%,rgba(13,13,13,1) 34%,rgba(1,1,1,1) 50%,rgba(10,10,10,1) 53%,rgba(78,78,78,1) 76%,rgba(56,56,56,1) 87%,rgba(27,27,27,1) 100%);
  background: -o-linear-gradient(315deg, rgba(149,149,149,1) 0%,rgba(13,13,13,1) 34%,rgba(1,1,1,1) 50%,rgba(10,10,10,1) 53%,rgba(78,78,78,1) 76%,rgba(56,56,56,1) 87%,rgba(27,27,27,1) 100%);
  background: linear-gradient(135deg, rgba(149,149,149,1) 0%,rgba(13,13,13,1) 34%,rgba(1,1,1,1) 50%,rgba(10,10,10,1) 53%,rgba(78,78,78,1) 76%,rgba(56,56,56,1) 87%,rgba(27,27,27,1) 100%);
  color: whitesmoke;
  font-family: 'Alegreya Sans', sans-serif;
}





