.france{
    background-image: -webkit-gradient(linear, left top, right top, color-stop(33%, #21468B), color-stop(33%, #ffffff), color-stop(66%, #ffffff), color-stop(66%, #AE1C28) );
    background-image: -webkit-linear-gradient(left, #21468B 33%, #ffffff 33%, #ffffff 66%, #AE1C28 66% );
    background-image: -o-linear-gradient(left, #21468B 33%, #ffffff 33%, #ffffff 66%, #AE1C28 66% );
    background-image: linear-gradient(90deg, #21468B 33%, #ffffff 33%, #ffffff 66%, #AE1C28 66% );
    -webkit-filter: contrast(4);
    filter: contrast(4); 
}
.ireland{
    background-image: -webkit-gradient(linear, left bottom, left top, color-stop(33%, #009a49), color-stop(33%, #ffffff), color-stop(66%, #ffffff), color-stop(66%, #FF7900) );
    background-image: -webkit-linear-gradient(bottom, #009a49 33%, #ffffff 33%, #ffffff 66%, #FF7900 66% );
    background-image: -o-linear-gradient(bottom, #009a49 33%, #ffffff 33%, #ffffff 66%, #FF7900 66% );
    background-image: linear-gradient(0deg, #009a49 33%, #ffffff 33%, #ffffff 66%, #FF7900 66% );
    -webkit-filter: contrast(4);
    filter: contrast(4); 
}
.belgium{
    background-image: -webkit-gradient(linear, left bottom, left top, color-stop(33%, #000), color-stop(33%, #ffe936), color-stop(66%, #ffe936), color-stop(66%, #ff0f21) );
    background-image: -webkit-linear-gradient(bottom, #000 33%, #ffe936 33%, #ffe936 66%, #ff0f21 66% );
    background-image: -o-linear-gradient(bottom, #000 33%, #ffe936 33%, #ffe936 66%, #ff0f21 66% );
    background-image: linear-gradient(0deg, #000 33%, #ffe936 33%, #ffe936 66%, #ff0f21 66% );
    -webkit-filter: contrast(4);
    filter: contrast(4); 
}
.estonia{
    background-image: -webkit-gradient(linear, right top, left top, color-stop(33%, #4891d9), color-stop(33%, #000000), color-stop(66%, #000000), color-stop(66%, #ffffff) );
    background-image: -webkit-linear-gradient(right, #4891d9 33%, #000000 33%, #000000 66%, #ffffff 66% );
    background-image: -o-linear-gradient(right, #4891d9 33%, #000000 33%, #000000 66%, #ffffff 66% );
    background-image: linear-gradient(-90deg, #4891d9 33%, #000000 33%, #000000 66%, #ffffff 66% );
    -webkit-filter: contrast(4);
    filter: contrast(4);
}
.austria{
    background: -webkit-gradient(linear, left top, left bottom, color-stop(33.3%, red), color-stop(33.3%, white), color-stop(66.6%, white), color-stop(66.6%, red));
    background: -webkit-linear-gradient(top, red 33.3%, white 33.3%, white 66.6%, red 66.6%);
    background: -o-linear-gradient(top, red 33.3%, white 33.3%, white 66.6%, red 66.6%);
    background: linear-gradient(-180deg, red 33.3%, white 33.3%, white 66.6%, red 66.6%);
    -webkit-filter: contrast(4);
    filter: contrast(4);
}
.spain{
    background-image: -webkit-linear-gradient(-90deg, #AA151B 25%, #F1BF00 25%, #F1BF00 75%, #AA151B 75%, #AA151B 100%);
    -webkit-filter: contrast(4);
    filter: contrast(4);
}
.ukraine{
    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, royalblue), color-stop(50%, yellow));
    background: -webkit-linear-gradient(top, royalblue 50%, yellow 50%);
    background: -o-linear-gradient(top, royalblue 50%, yellow 50%);
    background: linear-gradient(-180deg, royalblue 50%, yellow 50%);
    -webkit-filter: contrast(4);
    filter: contrast(4);
}

a{
    margin-bottom: 5%;
}
h1,h4{
    text-align: center;
}




